import { NavLink, useHistory } from "react-router-dom";
import "./Header.css";
import LogoImg from "../../../Assets/logo.jpg";
import axios from "axios";
import config from "../../../Configs/Configs";
import Search from "../../ShareArea/Search/Search";
import { useEffect, useState } from "react";
import { Button } from "@material-ui/core";
import Main from "../Main/Main";
import AutoComplete from "../../ShareArea/AutoComplete/AutoComplete";




function Header(): JSX.Element {
    const [products ,setProducts] =useState([]);
    const history =useHistory();
    useEffect(()=>{
        axios.get(config.urls.product).then((res)=>{setProducts(res.data)})
    })
    const loginHandler = () => {
        history.push("/login")
    }
    const logoutHandler = () => {
        axios.post(config.urls.login + "logout");
        sessionStorage.clear();
        history.push("/home");
    }
   const registerHandler =()=>{
       history.push("/register")
   }
    return (
        <div className="Header ">
            <div className="logobttn">
                <NavLink to="/home">YAM<img src={LogoImg} />  ROT</NavLink>
            </div>
            <div>
                <Main />
            </div>
            <div className ="searchbttn">
            <AutoComplete />
                
            </div>
            <div className="loginbttn">
                <Button onClick={loginHandler}>כניסה</Button>
                <Button onClick ={registerHandler}>הירשם</Button>
                <Button onClick={logoutHandler}>יציאה</Button>
            </div>
           
        </div>
    );
}

export default Header;
