import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { createStyles, makeStyles, Paper, Theme } from '@material-ui/core';
import AddShoppingCartIcon from '@material-ui/icons/AddShoppingCart';
import Cart from '../../Product/Cart/Cart';
import { minWidth } from '@mui/system';
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
      root:{
         direction:'rtl',
       width:'100%',
       height:'80%'


        

      },
       dialog:{
       height:'80%'
       },
    dialogButton: {
      margin:'3px',
    background:'#FDF2E9',

      marginTop:'10px'
      
      
    },
    dialogContentText:{
    fontFamily:'Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif',
    fontSize:'20px',
    margin:'5px',
    color:'black'
        
    },
    dialogTitle:{
      textAlign:'center',
      fontSize:'50px',
    fontFamily:'Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif',
    color:'black'


    },
    paper:{
      maxWidth:'80%'
    }
   

}));

export default function AdminLogDialog() {
  const [open, setOpen] = React.useState(false);
  const classes = useStyles();
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div className={classes.root}>
      <Button variant="outlined" onClick={handleClickOpen} className={classes.dialogButton}>
        <AddShoppingCartIcon />
      </Button>
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        className={classes.dialog}
        classes ={{paper:classes.paper}}
      >
        <DialogTitle id="alert-dialog-title" className={classes.dialogTitle}>{"המוצרים שלך"}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description" className={classes.dialogContentText}>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
          </Button>
           <Cart />
        </DialogActions>
      </Dialog>
    </div>
  );
}