import { Button, createStyles, makeStyles, Theme } from "@material-ui/core";
import axios from "axios";
import { useState } from "react";
import config from "../../../Configs/Configs";
import "./Cart.css";
import productModel from "../../../Modules/productModel";
import { useEffect } from "react";
import RecipeReviewCard from "../UseCard/UseCard";
import { useHistory } from "react-router-dom";
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
      root:{
      boxShadow:'rgba(0,0,0,0.35) 0px,5px,15px',
      justifyContent:'center',
      alignItems:'center',
      display:'inline-block',
      },
      span:{
          fontSize:'20px'
      }
    }))
function Cart(): JSX.Element {
    const classes = useStyles();
    const[userProduct ,setUserProduct] = useState<productModel[]>();
    useEffect(()=>{
            axios.get(config.urls.cart + "list").then((res)=>{
                if(res.data.msg){
                    return
                }
                setUserProduct(res.data);
               })
       

    },[])

    

    return (
        <div className={classes.root}>
            {userProduct?.map(p=><RecipeReviewCard product={p} key={p.id}  />)}
        </div>
    );
}

export default Cart;
