import { Button, createStyles, makeStyles, TextField, Theme } from "@material-ui/core";
import axios from "axios";
import { useState } from "react";
import config from "../../../Configs/Configs";
import Forum from "../../../Modules/forumModel";
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { useEffect } from "react";
const useStyles = makeStyles((theme:Theme)=>createStyles({
root:{
    display:'flex',
    direction:'rtl'
   
},
form:{
    marginTop:'20px',
    marginRight:'100px',
    margin:'20px',
    textAlign:'center',
   
},
textField:{
    width:'300px',
    color:'white'
},
button:{
    width:'100px',

},
table: {
    minWidth: '500px',
  },
  tableCell:{
    fontSize:'20px',
    textAlign:'left'
  },

tableCell2:{
  textAlign:'left',
  width:'200px'
}
}));




    

    

function Register(): JSX.Element {
    const classes = useStyles();
    const [username, setUserName] = useState();
    const [title, setTitle] = useState();
    const [text, setText] = useState();
    const [forums, setForums] = useState<Forum[]>();
    useEffect(() => {
     
            axios.get(config.urls.forum).then((res) => {
                if (res.data.length > 0) {
                    setForums(res.data)
                }
            }).catch((e)=>{alert(e)})

    })
    
    const usernameHandler = (event) => {
        setUserName(event.target.value);
    }
    const titleHandler = (event) => {
        setTitle(event.target.value);
    }
    const textHandler = (event) => {
        setText(event.target.value);
    }
    async function forumHandler() {

        if (username != null && title != null && text != null) {
            await axios.post(config.urls.forum, { username, title, text })
        }
    }
        

    return (
        <div className={classes.root}>
            <div className={classes.form}>

           
            <form method="POST">
                <h1>חווית משתמש</h1>
                <br />
                <br />
                <TextField variant="outlined" className={classes.textField} label="username" type="text" placeholder="username" required onChange={usernameHandler} />
                <br />
                <br />
                <br />
                <TextField variant="outlined" className={classes.textField} label="title" type="text" placeholder="title" required onChange={titleHandler} />
                <br />
                <br />
                <br />
                <TextField  variant="outlined" rows={6} multiline className={classes.textField} label="text" type="text" placeholder="text"  required onChange={textHandler} />
                <br />
                <br />
                <br />
                <Button className={classes.button} onClick={forumHandler}>send</Button>
            </form>
            </div>

            <div>
                {forums?.map(p=><div key={p.id}>
                    <div>
                       
                        <TableContainer component={Paper}>
                            <Table className={classes.table} aria-label="simple table">
                                <TableHead>
                                    <TableRow>
                                        <TableCell className={classes.tableCell} align="right">שם משתמש</TableCell>
                                        <TableCell className={classes.tableCell} align="right">כותרת</TableCell>
                                        <TableCell className={classes.tableCell} align="right">טקסט</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                        <TableRow key={p.id}>
                                            <TableCell className={classes.tableCell2} align="right">{p.username}</TableCell>
                                            <TableCell className={classes.tableCell2} align="right">{p.title}</TableCell>
                                            <TableCell className={classes.tableCell2} align="right">{p.text}</TableCell>
                                        </TableRow>
                                </TableBody>
                            </Table>
                        </TableContainer>

                    </div>
                </div>)}
            </div>
        </div>
            );
}
export default Register;
