import { Button, Card, CardActions, CardContent, CardHeader, createStyles, makeStyles, TextField, Theme } from "@material-ui/core";
import { useEffect, useState } from "react";
import axios from "axios";
import config from "../../../Configs/Configs";
import { Redirect, useHistory } from "react-router-dom";
import setHeader from "../../../Services/setHeaders"

const useStyles = makeStyles((theme:Theme)=>
createStyles({
  root:{
    display:'inline-flex',
    textAlign:'center',
    margin:'30px',
    padding:'30px',
    marginRight:'250px',
    width:'500px',
    border:'1px solid black',
    borderRadius: '20px',
    background:'#F2F3F4 '
    
  },
  form:{
    marginRight:'40px',
    marginTop:'20px'
  },
  textField:{
    width:'400px',
    textAlign:'center'
    
  },
  span:{
    fontSize:'20px',
    color:'red'
  }
  
}))



function Login(): JSX.Element {

  const classes = useStyles();
  const [username ,setUserName] = useState<string>();
  const [password ,setPassword] = useState<string>();
  const history = useHistory();
  const formData = new FormData();
  formData.append('username',username);
  formData.append('password',password);
  async function rendering(e) {
    if(e.keyCode ==13){
      if(username ==='' || username ===undefined || password===''||password===undefined){
        document.getElementById("validation").innerHTML = "these fields are required";
        setPassword('');
        setUserName('');
        return;
      }
      
      await axios.post(config.urls.login + "login", formData ,{headers:setHeader}).then((res) => {
        if (res.data.msg === "wrong") {
          document.getElementById("validation").innerHTML = "incorrect username or password ";
          setPassword('');
          setUserName('');
          return;
        }
        if (res.data.msg === "write") {
          sessionStorage.setItem('token', res.data.response.token);
          history.push("/home")
        }

      });
    }
    
   

  }
 
  const userNameHandler = (e) => {setUserName(e.target.value)}
  const passwordHandler = (e) => {setPassword(e.target.value)}

  return (
    <div className={classes.root}>
      <form method="POST" className ={classes.form}  >
       
            <TextField
            className={classes.textField}
              id="username"
              label="Username"
              placeholder="Username"
              onChange={userNameHandler}
              variant="outlined"
              required
               


            />
            <br />
            <br />

            <TextField
            className={classes.textField}
              id="password"
              type="password"
              label="Password"
              placeholder="Password"
              onChange={passwordHandler}
              variant="outlined"
              onKeyDown ={e=>rendering(e)}
            />
            <br />
            <br />
             <span className={classes.span} id ="validation"></span>
             <br />
             <br />        
            <Button
              onClick={rendering}
            id="button"
            onKeyPress ={rendering}
            >Login
            </Button>

        
      </form>


    </div>
  );
}


export default Login;
